@import 'scss/reset';
@import 'scss/base';
@import 'scss/nav';
@import 'scss/footer';

@import '../assets/fonts/webfonts/stylesheet.css';

$widthSideMenu: 30%;
$navHeight: 60px;
$transitionDuration: 0.6s;

body {
  background: black;
  font-family: 'Raleway', sans-serif;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 60px;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 10;
  height: $navHeight;
  mix-blend-mode: exclusion;
  color: white;
  transition: color 0.6s ease-out;
  .title {
    font-family: 'Playfair Display', serif;
    font-weight: bold;
    font-size: 2.5em;
    position: relative;
    top: -2px;
  }
  
  .burger {
    cursor: pointer;
  }
}


.side-content {
  position: fixed;
  top: 0;
  bottom: 0;
  width: $widthSideMenu;
  display: flex;
  justify-content: center;
  will-change: transform;
  ul {
    position: absolute;
    top: 80%;
    left: 60px;
    transform: translateY(calc(-50% - 3em));
    font-size: 3em;
    li {
      color: whitesmoke;
      margin-bottom: 1em;
      font-weight: 500;
      overflow: hidden;
      span {
        display: block;
        transform: translate3d(0, 100%, 0);
        transition: transform 400ms ease-out;
      }
    }
    li:nth-child(1) {
      span {
        transition-delay: $transitionDuration / 3;
      }
    }
    li:nth-child(2) {
      span {
        transition-delay: $transitionDuration / 3 + 100ms;
      }
    }
    li:nth-child(3) {
      span {
        transition-delay: $transitionDuration / 3 + 150ms;
      }
    }
  }
}

.main-content {
  visibility: visible;
  padding: $navHeight;
  height: 300vh;
  background: ghostwhite;
  z-index: 1;
  top: 0;
  bottom: 0;
  width: 100%;
  overflow: auto;
  text-align: center;
  transition: transform $transitionDuration ease-out, border-radius 0.6s ease-out, visibility $transitionDuration ease-out;
  img {
    border-radius: 10px;
  }
  section {
    margin-bottom: 24px;
  }
}
body.menu-open {
  .side-content {
    transform: translate3d(0, 0, 0);
    ul li span {
      transform: translate3d(0,0,0);
    }
  }
  .main-content {
    transform: translate3d($widthSideMenu, $navHeight, 0);
    border-radius: 20px;
  }
}
@media screen and (max-width: 1024px){
  body.menu-open {
    .main-content {
      transform: translate3d(100%, 50%, 0);
      visibility: hidden;
    }
  }
}
.menu-burger {
  align-items: center;
  display: flex;
  font-size: 0;
  background: none;
  border: none;
  cursor: pointer;
  font: inherit;
}
.dashes {
  align-items: center;
  display: flex;
  height: 26px;
  position: relative;
  width: 26px;
}
.dash {
  border-top: 1px solid;
  left: 50%;
  position: absolute;
  top: 50%;
  transform: translate(-50%, -50%);
  transform-style: preserve-3d;
  transition: transform 1s ease;
  width: 100%;
}
.dash:nth-child(1) {
  transform: translate(-50%,-4px);
}
.dash:nth-child(2) {
    display: block;
    left: 0;
    transform: none;
}
.dash:nth-child(3) {
  transform: translate(-50%,4px);
}
.burger {
  margin-left: 1.2rem;
}

.menu-open {
  .dash:nth-child(1) {
    transform: translate(-50%,-50%) rotate(-45deg);
  }
  .dash:nth-child(2) {
    transform: scaleX(0);
  }
  .dash:nth-child(3) {
    transform: translate(-50%,-50%) rotate(45deg);
  }
}